import React from "react";
import PropTypes from "prop-types";
import { utils } from "near-api-js";

const ProductDetails = ({ product, productDetails }) => {
  const {
    id,
    name,
    description,
    sold,
    image,
    owner,
    price,
    bidders,
    bids,
    created,
  } = product;
  console.log(product);
  const daysOfTheWeek = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
  const monthsOfTheYear = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

  const bidEndTimestamp =
    Number(product.created) + Number(product.duration) * 60000;
  const endTime = new Date(Number(bidEndTimestamp));
  const createdTime = new Date(Number(created));

  const formattedTime = (time) => {
    return `${time.getHours()}:${time.getMinutes()}:${time.getSeconds()}`;
  }
  const formattedDate = (date) => {
    return `${daysOfTheWeek[date.getDay()]} ${date.getDate()} ${monthsOfTheYear[date.getMonth()]}, ${date.getFullYear()}`;
  }

  // console.log(formattedTime, formattedDate);

  const biddersDetails = () => {
    let details = [];
    for (let i = 0; i < bidders.length; i++) {
      let eachBidders = {};
      eachBidders.bidder = bidders[i];
      eachBidders.bid = bids[i];

      details.push(eachBidders);
    }
    return details;
  };

  return (
    <>
      <p
        onClick={() => {
          productDetails(false);
        }}
        style={{cursor: "pointer"}}
      >
        Back
      </p>
      <div key={id}>
        <div className="">
          <img
            src={image}
            width={400}
            height={300}
            alt={name}
            style={{ objectFit: "cover" }}
          />
        </div>
        <div className="mt-4" style={{ width: "400px" }}>
          <table>
            <tbody>
              <tr>
                <td>Product ID:</td>
                <td>{id}</td>
              </tr>
              <tr>
                <td>Owner:</td>
                <td>{owner}</td>
              </tr>
              <tr>
                <td>Name:</td>
                <td>{name}</td>
              </tr>
              <tr>
                <td>Description:</td>
                <td>{description}</td>
              </tr>
              <tr>
                <td>Created:</td>
                <td>{formattedDate(createdTime)} {formattedTime(createdTime)}</td>
              </tr>
              <tr>
                <td>Ends:</td>
                <td>{formattedDate(endTime)} {formattedTime(endTime)}</td>
              </tr>
              <tr>
                <td>Duration:</td>
                <td>{product.duration} minutes</td>
              </tr>
              <tr>
                <td>Price:</td>
                <td>{utils.format.formatNearAmount(price)} NEAR</td>
              </tr>
              <tr>
                <td>Sold:</td>
                <td>{sold.toString().toUpperCase()}</td>
              </tr>
              <tr>
                <td>Bidders:</td>
                <td>
                  {bidders.length > 0 ? (
                    <>
                      {biddersDetails().map((bidder, index) => (
                        <p className="m-0" key={index}>
                          <span className="">Bidder: {bidder.bidder}</span>
                          <span>Bid: {utils.format.formatNearAmount(bidder.bid)} NEAR</span>
                        </p>
                      ))}
                    </>
                  ) : (
                    <p className="m-0">No bidders</p>
                  )}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
};

ProductDetails.propTypes = {
  product: PropTypes.instanceOf(Object).isRequired,
};

export default ProductDetails;
